import { Component, OnInit, AfterContentChecked } from '@angular/core';
import { Router } from '@angular/router';
import { DatePipe, CurrencyPipe } from '@angular/common';
import { Subject } from 'rxjs';
import * as dayjs from 'dayjs';
import 'dayjs/locale/id';
import {
  BalanceService,
  ListHistoryBalanceResponse,
  listHistoryBalanceInputDto,
  StatusKyc,
  RegisterService,
  MerchantRegistration
} from '@youtap-web-lib';
import 'dayjs/locale/id';
import { AuthData } from '../../../../shared/data/auth.data';
import { GaService } from '../../../../shared/service/ga.service';
import { WindowActionService } from '../../../../shared/service/window-action.service';
import { ModalService } from '../../../../shared/service/modal/modal.service';
import { KycVerifModalComponent } from '../../kyc-verif-modal/kyc-verif-modal.component';
import { TncAjakCuanModalComponent } from '../../tnc-ajak-cuan-modal/tnc-ajak-cuan-modal.component';
dayjs.locale('id');

@Component({
  selector: 'detail-withdraw-commission',
  templateUrl: './detail-withdraw-commission.component.html',
  styleUrl: './detail-withdraw-commission.component.scss'
})
export class DetailWithdrawCommissionComponent
  implements OnInit, AfterContentChecked
{
  dateRange!: Date[];
  minDate!: Date;
  maxDate!: Date;
  displayCalendar: boolean = false;
  isOpen = false;
  rangeDate: string = '3 Sep 2023 - 3 Okt 2023';
  label: string = 'Rentang waktu maks. 31 hari.';
  loading: boolean = false;
  loadingProfile = false;
  totalCommission: number = 0;
  page: number = 1;
  search: string = '';
  dataListHistoryBalance!: ListHistoryBalanceResponse;
  userId!: string | undefined;
  loadingKycStatus = false;
  kycStatus!: StatusKyc | undefined;
  showStatusKycInCheck = false;
  showStatusKycRejected = false;
  balance = 0;
  accountType!: string;
  merchantRegistration!: MerchantRegistration;

  constructor(
    private router: Router,
    private datePipe: DatePipe,
    private balanceService: BalanceService,
    private currencyPipe: CurrencyPipe,
    private authData: AuthData,
    private gaService: GaService,
    private registerService: RegisterService,
    private windowActionService: WindowActionService,
    private modalService: ModalService
  ) {}

  ngOnInit(): void {
    this.minDate = new Date();
    const millisecondsInADay = 24 * 60 * 60 * 1000;
    this.maxDate = new Date(this.minDate.getTime() - 31 * millisecondsInADay);
    this.setDefaultDate();
    this.getAffiliateBalance();
    this.getStatusKyc();

    this.userId = this.authData.user?.id;
    this.accountType = this.authData.user?.merchant.accountType;
    const startDate = dayjs(this.maxDate).format('YYYY-MM-DD');
    const endDate = dayjs(this.minDate).format('YYYY-MM-DD');
    this.getListHistoryBalance(startDate, endDate);
  }

  ngAfterContentChecked() {
    if (this.balance !== this.totalCommission) {
      this.balance = this.totalCommission;
      const startDate = dayjs(this.maxDate).format('YYYY-MM-DD');
      const endDate = dayjs(this.minDate).format('YYYY-MM-DD');
      this.getListHistoryBalance(startDate, endDate);
    }
  }

  ionViewWillEnter() {
    this.getAffiliateBalance();
  }

  getAffiliateBalance() {
    this.loading = true;
    this.balanceService.getAffiliateBalance().subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.totalCommission = data.getAffiliateBalance.balance;
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  getListHistoryBalance(startDate: string, endDate: string) {
    this.loading = true;
    const data: listHistoryBalanceInputDto = {
      listHistoryBalanceInput: {
        filter: {
          startDate: startDate ? startDate : '',
          endDate: endDate ? endDate : '',
          userId: this.userId ? this.userId : ''
        },
        order: {
          orderBy: 'CREATED_AT',
          sortBy: 'DESC'
        },
        limit: 25,
        page: this.page,
        search: this.search
      }
    };

    this.balanceService.listHistoryBalance(data).subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.dataListHistoryBalance = data.listHistoryBalance;
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  getStatusKyc() {
    this.loadingKycStatus = true;
    this.registerService.getStatusKyc().subscribe({
      next: ({ data }) => {
        this.loadingKycStatus = false;
        this.merchantRegistration = data.getStatusKyc.merchantRegistration;
        this.kycStatus =
          data.getStatusKyc.merchantRegistration?.merchantKyc.status;
      },
      error: () => {
        this.loadingKycStatus = false;
      }
    });
  }

  redirectSaldoPage() {
    if (
      this.accountType == 'PREMIUM_MERCHANT' ||
      this.kycStatus === 'SUCCESS'
    ) {
      this.router.navigate(['/ajakcuan/saldo-withdraw-commission'], {});
    }
    if (
      (this.accountType == 'BASIC_MERCHANT' &&
        this.merchantRegistration === null) ||
      (this.accountType == 'BASIC_MERCHANT' && this.kycStatus === 'PENDING')
    ) {
      this.gaService.addEvent(
        'tarik_komisi_not_kyc',
        'ajakcuan',
        '',
        true
      );
      const modalRef = this.modalService.open(KycVerifModalComponent, {
        title: '',
        closeButton: true
      });
      modalRef.onResult().subscribe((data) => {
        if (data) {
          const modalRef2 = this.modalService.open(TncAjakCuanModalComponent, {
            title: 'Syarat & Ketentuan',
            closeButton: true
          });
          modalRef2.onResult().subscribe((data) => {
            if (data) {
              this.redirectSaldoPage();
            }
            this.gaService.addEvent(
              'Ajakcuan_click_syarat_ketentuan',
              'ajakcuan',
              '',
              true
            );
          });
        }
      });
    }
    if (this.kycStatus !== undefined) {
      if (this.kycStatus === 'IN_CHECK') {
        this.gaService.addEvent(
          'tarik_komisi_kyc_on_prog',
          'ajakcuan',
          '',
          true
        );
        this.showStatusKycInCheck = true;
      } else if (this.kycStatus === 'REJECTED') {
        this.gaService.addEvent(
          'tarik_komisi_kyc_ditolak',
          'ajakcuan',
          '',
          true
        );
        this.showStatusKycRejected = true;
      } else {
        this.router.navigate(['/ajakcuan/saldo-withdraw-commission'], {});
      }
    }
  }

  closeModalStatusKycInCheck() {
    this.gaService.addEvent(
      'click_ok_kyc_onprog',
      'ajakcuan',
      '',
      true
    );
    this.showStatusKycInCheck = false;
  }

  showCalender() {
    this.gaService.addEvent(
      'Ajakcuan_click_filter_calender',
      'ajakcuan',
      '',
      true
    );
    this.displayCalendar = !this.displayCalendar;
  }

  handleDateSelect(data: Date[]) {
    if (data[0] && data[1]) {
      this.displayCalendar = false;
      this.rangeDate = `${this.convertDateTimeToString(
        data[0]
      )} - ${this.convertDateTimeToString(data[1])}`;
      const startDate = dayjs(data[0]).format('YYYY-MM-DD');
      const endDate = dayjs(data[1]).format('YYYY-MM-DD');
      this.getListHistoryBalance(startDate, endDate);
    }
  }

  convertDateTimeToString(date: Date) {
    const dateObj = new Date(date);
    const formattedDate = this.datePipe.transform(dateObj, 'd MMM yyyy');
    return formattedDate;
  }

  setFormattedDate(date: Date) {
    const datetime = date;
    const wibTime = dayjs(datetime).add(-7, 'hour');
    const formattedDateTime = dayjs(wibTime)
      .locale('id')
      .format('DD MMM YYYY HH:mm [WIB]');
    return formattedDateTime;
  }

  setDefaultDate() {
    this.rangeDate = `${this.convertDateTimeToString(
      this.maxDate
    )} - ${this.convertDateTimeToString(this.minDate)}`;
  }

  formattedRupiah(value: string) {
    return value.replace(/,/g, '.');
  }

  formatCommissionNumber(value: number, currency: string, symbol: string) {
    const valueRupiah =
      this.currencyPipe.transform(value, currency, symbol, '1.0-0') || '';
    return this.formattedRupiah(valueRupiah);
  }

  formatCommissionString(value: string, currency: string, symbol: string) {
    const toString = value.toString();
    const numericValue = toString.replace(/[^\d]/g, '');
    const valueRupiah =
      this.currencyPipe.transform(
        parseFloat(numericValue),
        currency,
        symbol,
        '1.0-0'
      ) || '';
    return this.formattedRupiah(valueRupiah);
  }

  myRefreshEvent(event: Subject<void>): void {
    const millisecondsInADay = 24 * 60 * 60 * 1000;
    const minDate = new Date();
    const maxDate = new Date(minDate.getTime() - 31 * millisecondsInADay);
    const startDate = dayjs(maxDate).format('YYYY-MM-DD');
    const endDate = dayjs(minDate).format('YYYY-MM-DD');
    this.rangeDate = `${this.convertDateTimeToString(
      maxDate
    )} - ${this.convertDateTimeToString(minDate)}`;
    this.getAffiliateBalance();
    this.getListHistoryBalance(startDate, endDate);
    setTimeout(() => {
      event.next();
    }, 2000);
  }

  handleWhatsApp() {
    this.gaService.addEvent(
      'user_click_hubungi_cs',
      'ajakcuan',
      '',
      true
    );
    const url: string = 'https://api.whatsapp.com/send?phone=628111500523';
    this.windowActionService.open(url, '_blank');
  }
}
