import { Component } from '@angular/core';
import { InputModalDto } from '../../../shared/service/modal/modal.service';
import { Modal } from '../../../shared/service/modal/modal.model';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'share-pos-modal',
  templateUrl: './share-pos-modal.component.html',
  styleUrl: './share-pos-modal.component.scss'
})
export class SharePosModalComponent extends Modal {
  title!: string;
  showButton = false;
  isLoading = false;
  from = '';

  constructor(private gaService: GaService) {
    super();
  }
  onInjectInputs(inputs: InputModalDto): void {
    this.title = inputs.title;
    this.showButton = inputs.closeButton;
  }

  cancel(): void {
    this.gaService.addEvent(
      'Ajakcuan_click_button_cross_inyoutap_pos' + this.from,
      'ajakcuan',
      '',
      true
    );
    this.dismiss();
  }
}
