import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AjakcuanResolver } from '../../shared/resolvers/ajakcuan.resolver';

import { BasicContainerComponent } from '../../shared/layout/container/basic-container/basic-container.component';
import { BoardingAjakCuanComponent } from './boarding-ajak-cuan/boarding-ajak-cuan.component';
import { DetailWithdrawCommissionComponent } from './withdraw-commission/detail-withdraw-commission/detail-withdraw-commission.component';
import { SaldoWithdrawCommissionComponent } from './withdraw-commission/saldo-withdraw-commission/saldo-withdraw-commission.component';
import { EducationComponent } from './education/education.component';
import { EducationBosComponent } from './education-bos/education-bos.component';
import { EducationPosComponent } from './education-pos/education-pos.component';
import { DashboardAjakCuanComponent } from './dashboard-ajak-cuan/dashboard-ajak-cuan.component';
import { EducationPosLandingpageComponentComponent } from './education-pos/education-pos-landingpage/education-pos-landingpage-component.component';
import { SuccessRegisterComponent } from './success-register/success-register.component';
import { BenefitAjakCuanComponent } from './benefit-ajak-cuan/benefit-ajak-cuan.component';
import { ProfileResolver } from "../../shared/resolvers/profile.resolver";
import { DetailProductComponent } from './detail-product/detail-product.component';
import { FooterAjakCuanComponent } from "./footer-ajak-cuan/footer-ajak-cuan.component";
import { PerformanceAjakCuanComponent } from "./performance-ajak-cuan/performance-ajak-cuan.component";
import { PinAjakCuanComponent } from "./pin-ajak-cuan/pin-ajak-cuan.component";

const routes: Routes = [
  {
    path: '',
    component: FooterAjakCuanComponent,
    resolve: {
      data: AjakcuanResolver
    },
    children: [
      {
        path: 'dashboard',
        component: DashboardAjakCuanComponent,
        title: 'Dashboard Ajak Cuan',
        data: {
          pageTitle: 'Ajak Cuan',
          header: true,
        }
      },
      {
        path: 'education',
        component: EducationComponent,
        title: 'Edukasi',
        data: {
          pageTitle: 'Edukasi',
          header: true,
        }
      },
      {
        path: 'education/pos',
        component: EducationPosComponent,
        title: 'Youtap POS',
        data: {
          pageTitle: 'Youtap POS'
        }
      },
      {
        path: 'education/bos',
        component: EducationBosComponent,
        title: 'Youtap BOS',
        data: {
          pageTitle: 'Youtap BOS'
        }
      },
      {
        path: 'performance',
        component: PerformanceAjakCuanComponent,
        title: 'Performa',
        data: {
          pageTitle: 'performa',
        }
      },
    ]
  },
  {
    path: 'onboard',
    component: BoardingAjakCuanComponent,
    title: 'Ajak Cuan',
    data: {
      pageTitle: 'Ajak Cuan',
      header: true,
    }
  },
  {
    path: 'succeed',
    component: SuccessRegisterComponent,
    title: 'Ajak Cuan',
    data: {
      pageTitle: 'Ajak Cuan',
      header: false
    }
  },
  {
    path: 'benefit',
    component: BenefitAjakCuanComponent,
    title: 'Keuntungan Ajak Cuan',
    data: {
      pageTitle: 'Keuntungan Ajak Cuan'
    }
  },
  {
    path: 'education-pos',
    component: EducationPosComponent,
    title: 'Youtap POS',
    data: {
      pageTitle: 'Youtap POS'
    }
  },
  {
    path: 'education-bos',
    component: EducationBosComponent,
    title: 'Youtap BOS',
    data: {
      pageTitle: 'Youtap BOS'
    }
  },
  {
    path: 'detail-withdraw-commission',
    component: DetailWithdrawCommissionComponent,
    title: 'Tarik-Komisi-Ajak-Cuan',
    resolve: {
      profile: ProfileResolver
    },
    data: {
      pageTitle: 'Tarik Komisi'
    }
  },
  {
    path: 'saldo-withdraw-commission',
    component: SaldoWithdrawCommissionComponent,
    title: 'Tarik-Komisi-Ajak-Cuan',
    data: {
      pageTitle: 'Tarik Komisi'
    }
  },
  {
    path: '',
    component: BasicContainerComponent,
    data: { canGoBack: true },
    children: []
  },
  {
    path: 'education-link-recipient',
    component: EducationPosLandingpageComponentComponent,
    title: 'Education Pos',
    data: {
      pageTitle: 'Youtap POS'
    }
  },
  {
    path: 'detail-product/:variantSlug',
    component: DetailProductComponent,
    title: 'Detail Produk',
    data: {
      pageTitle: 'Detail Produk'
    }
  },
  {
    path: 'input-pin',
    component: PinAjakCuanComponent,
    title: 'PIN Youtap',
    data: {
      pageTitle: 'Pin Youtap'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AjakcuanRoutingModule { }
