import { AfterContentChecked, Component, OnInit, NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import {
  AjakCuanService,
  BalanceService,
  BannerService,
  IResponseListBannerDto,
  ItemsAjakCuanSCM,
  ItemSubsAjakCuan,
  ItemType,
  ListItemDto,
  PlanOption,
  ProfileService,
  RequestListBannerDto,
  RequestListItemDto,
  ResponseGetProfileDto,
  ResponseListItemDto,
  ResponseListItemSubDto,
  ScmAjakCuanService,
} from '@youtap-web-lib';
import { AuthData } from '../../../shared/data/auth.data';
import { ShareBosModalComponent, ShareBosModalDto } from '../share-bos-modal/share-bos-modal.component';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { environment } from '../../../../environments/environment.prod';
import { GaService } from '../../../shared/service/ga.service';
import { Subject } from "rxjs";

@Component({
  selector: 'dashboard-ajak-cuan',
  templateUrl: './dashboard-ajak-cuan.component.html'
})
export class DashboardAjakCuanComponent implements OnInit, AfterContentChecked {
  address!: string;
  balance!: number;
  loadingSection = false;
  loadingProfile = false;
  loadingBalance = false;
  loadingPlan = false;
  loadingListItem = false;
  lisBanner!: IResponseListBannerDto;
  dataProfile!: ResponseGetProfileDto;
  dataPlan!: ResponseListItemSubDto;
  dataPlanOption: PlanOption[] = [];
  items: ItemsAjakCuanSCM[] = [];
  listItem!: ResponseListItemDto;
  sectionSelect = 'all';
  lat = 0;
  page = 1;
  hasMore = true;
  itemTypeBos?: ItemType;
  showEmpty: boolean = false;
  from = '';
  search = '';
  searchString = '';
  showEmptySearch: boolean = false;
  suppliers: ItemsAjakCuanSCM[] = [];
  isPullToRefreshEnabled: boolean = true;
  isDisableSearch = false;
  labelEmptyState!: string;
  descEmptyState!: string;
  emptyStateSupllier = false;

  constructor(
    private router: Router,
    private authData: AuthData,
    private location: Location,
    private profileService: ProfileService,
    private balanceService: BalanceService,
    private scmAjakCuanService: ScmAjakCuanService,
    private modalService: ModalService,
    private bannerService: BannerService,
    private ajakCuanService: AjakCuanService,
    private gaService: GaService,
    private ngZone: NgZone
  ) { }

  ngOnInit() {
    this.lat = this.authData.location.latitude;
    this.getDataDashboard();
  }

  ngAfterContentChecked() {
    if (this.lat !== this.authData.location.latitude) {
      this.items = [];
      this.lat = this.authData.location.latitude;
      this.getDataDashboard();
    }
  }

  getDataDashboard() {
    this.getListSection();
    this.getProfileData();
    this.getListPlan();
    this.getBalance();
    this.getListItem();
    this.getListSupplier();
    if (Object.keys(this.authData.location).length) {
      const location = this.authData.location;
      this.address = `${location.district.name}, ${location.regency.name}`;
    }
  }

  goToWithdrawPage() {
    this.gaService.addEvent(
      'Ajakcuan_click_tarik_komisi',
      'ajakcuan',
      '',
      true
    );
    this.router.navigate(['/ajakcuan/detail-withdraw-commission']);
  }

  setLocation() {
    this.router.navigate(['/address/list']);
  }

  goBack() {
    this.location.back();
  }

  goToEducation() {
    this.gaService.addEvent(
      'Ajakcuan_click_edukasi' + this.from,
      'ajakcuan',
      '',
      true
    );
    this.router.navigate(['/ajakcuan/education']);
  }

  getListSection() {
    this.loadingSection = true;
    const requestBanner: RequestListBannerDto = {
      listBannerInput: {
        platform: 'AFFILIATE'
      }
    };
    this.bannerService.listBanner(requestBanner).subscribe({
      next: ({ data }) => {
        this.loadingSection = false;
        this.lisBanner = data;
      },
      error: () => {
        this.loadingSection = false;
      }
    });
  }

  getProfileData() {
    this.loadingProfile = true;
    this.profileService.getProfile().subscribe({
      next: ({ data }) => {
        this.loadingProfile = false;
        if (data) {
          this.dataProfile = data;
        }
      },
      error: () => {
        this.loadingProfile = false;
      }
    });
  }

  getBalance() {
    this.loadingBalance = true;
    this.balanceService.getAffiliateBalance().subscribe({
      next: (data) => {
        this.loadingBalance = false;
        if (data) {
          this.balance = data.data.getAffiliateBalance.balance;
        }
      },
      error: () => {
        this.loadingBalance = false;
      }
    });
  }

  getListPlan() {
    this.loadingPlan = true;
    const dataPlan: RequestListItemDto = {
      listItemInput: {
        page: 1,
        limit: 25,
        filter: {
          itemType: 'ITEM_TYPE_SUBSCRIPTION'
        }
      }
    };

    this.ajakCuanService.listItem(dataPlan).subscribe({
      next: ({ data }) => {
        this.loadingPlan = false;
        const currentDate = new Date();
        const filteredItems = data.listItem.items.filter((item) => {
          if (item.periodEnd !== null) {
            const periodEnd = new Date(item.periodEnd);
            return periodEnd > currentDate;
          }
          return true;
        });

        this.dataPlan = {
          listItem: {
            page: data.listItem.page,
            limit: data.listItem.limit,
            total: data.listItem.total,
            totalPage: data.listItem.totalPage,
            items: filteredItems
          }
        };
      },
      error: () => {
        this.loadingPlan = false;
      }
    });
  }

  getListItem() {
    this.loadingListItem = true;
    const data: ListItemDto = {
      listItemInput: {
        page: this.page,
        limit: 25,
        filter: {
          itemType: this.itemTypeBos
        },
        search: this.search
      }
    };

    this.scmAjakCuanService.listItem(data).subscribe({
      next: (data) => {
        this.loadingListItem = false;
        this.listItem = data.data;
        const currentDate = new Date();
        if (
          this.listItem &&
          this.listItem.listItem &&
          this.listItem.listItem.items
        ) {
          const itemsAjakCuan = data.data.listItem.items
            .filter((item) => {
              if (item.periodEnd === null) {
                return true;
              }

              if (typeof item.periodEnd === 'string') {
                const periodEnd = new Date(item.periodEnd);
                return periodEnd.getTime() > currentDate.getTime();
              } else {
                return false;
              }
            })
            .filter((item) => item.productVariant !== null)
            .filter((variants) =>
              variants.productVariant.productVariantWarehouses.find(
                (status) => status.status === 'ACTIVE'
              )
            );

          if ((this.page < this.listItem.listItem.totalPage) && !itemsAjakCuan.length) {
            this.page += 1;
            this.getListItem();
          } else {
            if (itemsAjakCuan.length) {
              this.showEmpty = false;
              this.showEmptySearch = false;
              itemsAjakCuan.forEach((data) => {
                const item: ItemsAjakCuanSCM = {
                  id: data.id,
                  type: data.type,
                  amount: data.amount,
                  periodEnd: data.periodEnd,
                  periodStart: data.periodStart,
                  status: data.status,
                  maxPrice: data.maxPrice,
                  priceType: data.priceType,
                  productVariant: data.productVariant,
                };
                this.items.push(item);
              });
            } else {
              if ((this.page === this.listItem.listItem.totalPage) && !itemsAjakCuan.length && this.searchString) {
                this.showEmptySearch = true;
                this.showEmpty = false;
              } else if (!this.items.length && (this.page === this.listItem.listItem.totalPage) && !itemsAjakCuan.length && !this.searchString) {
                this.showEmpty = true;
                this.showEmptySearch = false;
              }
            }
          }
        }
      },
      error: () => {
        this.loadingListItem = false;
        if (this.searchString) {
          this.showEmptySearch = true;
          this.showEmpty = false;
        }
      }
    });
  }

  onScroll() {
    if (this.page < this.listItem.listItem.totalPage) {
      this.page += 1;
      this.showEmpty = false;
      this.getListItem();
    }
  }

  onScrollHandling(event: Event) {
    const target = event.target as HTMLElement;
    const isTop = target.scrollTop === 0;
    this.isPullToRefreshEnabled = isTop;
  }

  changeSection(section: string) {
    this.page = 1;
    this.items = [];
    this.sectionSelect = section;
    this.search = '';
    this.searchString = '';
    this.showEmpty = false;
    this.showEmptySearch = false;
    if (section === 'package') {
      this.gaService.addEvent(
        'Ajakcuan_click_filter_paket_usaha_indashboard',
        'ajakcuan',
        '',
        true
      );
      this.itemTypeBos = ItemType.ITEM_TYPE_HIGHLIGHT;
    } else if (section == 'btok') {
      this.gaService.addEvent(
        'Ajakcuan_click_filter_product_indashboard',
        'ajakcuan',
        '',
        true
      );
      this.itemTypeBos = ItemType.ITEM_TYPE_PRODUCT_VARIANT;
    } else if (section == 'supplier') {
      this.gaService.addEvent(
        'Ajakcuan_click_section_supplier',
        'ajakcuan',
        '',
        true
      );
      this.itemTypeBos = ItemType.ITEM_TYPE_SUPPLIER;
      const checkItemSupplier = this.listItem.listItem.items.filter(item => item.type === ItemType.ITEM_TYPE_SUPPLIER);
      if (checkItemSupplier.length === 0) {
        this.emptyStateSupllier = true;
        this.labelEmptyState = 'Belum ada supplier yang bisa di promosikan';
        this.descEmptyState = 'Nantikan supplier...'
      }
    } else {
      this.gaService.addEvent(
        'Ajakcuan_click_filter_semua_indashboard',
        'ajakcuan',
        '',
        true
      );
      this.itemTypeBos = undefined;
    }
    this.getListItem();
  }

  generateLinkTracker(type: string) {
    if (type === 'product') {
      this.gaService.addEvent(
        'Ajakcuan_click_bagikan_indashboard',
        'ajakcuan',
        '',
        true
      );
    } else if (type === 'supplier') {
      this.gaService.addEvent(
        'Ajakcuan_click_supplier_indashboard',
        'ajakcuan',
        '',
        true
      );
    } else {
      this.gaService.addEvent(
        'Ajakcuan_click_bagikan_inpaket_extra_mantap',
        'ajakcuan',
        '',
        true
      );
    }
  }

  generateLink(
    type: string,
    product?: ItemsAjakCuanSCM,
    subscription?: ItemSubsAjakCuan,
    supplier?: ItemsAjakCuanSCM,
  ) {
    this.generateLinkTracker(type);
    let dataProduct!: ShareBosModalDto;
      if (type === 'product' && product) {
        dataProduct = {
          name: product.productVariant.name,
          commission: product.amount,
          image: product.productVariant.product.image,
          priceType: product.priceType,
          periodEnd: product.periodEnd,
          link: environment.BOS_URL + '/product/variant-detail/' + product?.productVariant.slug,
        };
      } else if (type === 'subscription' && subscription) {
        dataProduct = {
          name: subscription.planOption.plan.displayName,
          commission: subscription.amount,
          image: subscription.planOption.image,
          priceType: subscription.priceType,
          periodEnd: subscription.periodEnd,
          link: environment.BOS_URL + '/subscription/list-subscription',
          planOptionId: subscription.planOption.id,
        };
      } else if (type === 'supplier' && supplier) {
        dataProduct = {
          name: supplier.supplier ? supplier.supplier.name : '',
          commission: supplier.supplier ? supplier.amount : 0,
          image: supplier.supplier ? supplier.supplier.image : '',
          priceType: supplier.priceType,
          periodEnd: supplier.periodEnd,
          imageTier: supplier.supplier?.tier,
          link: environment.BOS_URL + '/supplier/detail?ss=' + supplier.supplier?.slug,
          supplierSlug: supplier.supplier?.slug,
        }
      }

    const modalRef = this.modalService.open(ShareBosModalComponent, {
      title: type === 'product' ? 'Bagikan dan promosikan produk ini' : type === 'supplier' ? 'Bagikan Supplier' : 'Bagikan Subscription',
      closeButton: true,
      data: {
        dataProduct: dataProduct,
        type: type,
        trackFrom: '',
        productId: product ? product.id : '',
        subscriptionId: subscription ? subscription.id : '',
        supplierId: supplier ? supplier.id : '',
      }
    });
    modalRef.onResult().subscribe(
      () => {
        // eslint-disable-next-line no-console
        console.log(type);
      },
      (dismissed) => {
        if (type === 'product') {
          this.gaService.addEvent(
            'Ajakcuan_click_button_cross_indashboard',
            'ajakcuan',
            '',
            true
          );
        } else {
          this.gaService.addEvent(
            'Ajakcuan_click_button_cross_inpaket_extra_mantap',
            'ajakcuan',
            '',
            true
          );
        }
        dismissed;
      }
    );
  }

  goDetailProduct(
    slug: string,
    itemId: string,
    priceType: string,
    price: number,
    periodEnd: string | null
  ) {
    this.gaService.addEvent(
      'Ajakcuan_click_product_indashboard',
      'ajakcuan',
      '',
      true
    );
    this.router
      .navigate([`ajakcuan/detail-product/${slug}`], {
        queryParams: {
          id: itemId,
          type: priceType,
          price: price,
          periodEnd: periodEnd
        }
      })
      .then(() => { });
  }

  myRefreshEvent(event: Subject<void>): void {
    this.ngZone.run(() => {
      this.page = 1;
      this.items = [];
      this.suppliers = [];
      this.sectionSelect = 'all';
      this.itemTypeBos = ItemType.ITEM_TYPE_PRODUCT_VARIANT;
      this.search = '';
      this.searchString = '';
      this.getDataDashboard();
      setTimeout(() => {
        event.next();
        this.isPullToRefreshEnabled = true;
      }, 2000);
    })
  }

  searchProduct() {
    this.isDisableSearch = true;
    this.page = 1;
    this.items = [];
    this.searchString = this.search;
    this.getListItem();
    setTimeout(() => {
      this.isDisableSearch = false;
    }, 500)
  }

  getListSupplier() {
    const data: ListItemDto = {
      listItemInput: {
        page: 1,
        limit: 25,
        filter: {
          itemType: ItemType.ITEM_TYPE_SUPPLIER
        },
        search: this.search
      }
    };

    this.scmAjakCuanService.listItem(data).subscribe({
      next: (data) => {
        if (data) {
          data.data.listItem.items.forEach((data) => {
            const item: ItemsAjakCuanSCM = {
              id: data.id,
              type: data.type,
              amount: data.amount,
              periodEnd: data.periodEnd,
              periodStart: data.periodStart,
              status: data.status,
              maxPrice: data.maxPrice,
              priceType: data.priceType,
              productVariant: data.productVariant,
              supplier: data.supplier,
            };
            this.suppliers.push(item);
          })
        }
      }
    })
  }
}
