import { Component, OnInit } from '@angular/core';
import {
  CreateAgentInputDto,
  RegisterService,
  StatusKyc,
  AjakCuanService,
  ProfileService
} from '@youtap-web-lib';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup } from '@angular/forms';
import { GaService } from '../../../shared/service/ga.service';
import {ModalService} from "../../../shared/service/modal/modal.service";
import {TncAjakCuanModalComponent} from "../tnc-ajak-cuan-modal/tnc-ajak-cuan-modal.component";

@Component({
  selector: 'boarding-ajak-cuan',
  templateUrl: './boarding-ajak-cuan.component.html'
})
export class BoardingAjakCuanComponent implements OnInit {
  check = false;
  loadingCreateAgent = false;
  loadingProfile = false;

  kycStatus!: StatusKyc;
  dataProfile!: string;
  createAgentForm!: FormGroup;

  errorMessage = '';
  defaultErrorMessage =
    'Cannot return null for non-nullable field Agent.referralCode.';
  invalidReferral = false;

  constructor(
    private router: Router,
    private registerService: RegisterService,
    private ajakCuanService: AjakCuanService,
    private formBuilder: FormBuilder,
    private profileService: ProfileService,
    private gaService: GaService,
    private modalService: ModalService,
  ) {
    this.createAgentForm = this.formBuilder.group({
      referralCode: ['']
    });
  }

  ngOnInit() {
    this.getStatusKyc();
    this.getProfileData();
  }

  handleCheck() {
    this.check = !this.check;
    if (this.check === true) {
      this.gaService.addEvent(
        'Ajakcuan_click_syarat_ketentuan',
        'ajakcuan',
        '',
        true
      );
    }
  }

  getProfileData() {
    this.loadingProfile = true;
    this.profileService.getProfile().subscribe({
      next: ({ data }) => {
        this.loadingProfile = false;
        this.dataProfile = data.detailUser.merchant.accountType;
      },
      error: () => {
        this.loadingProfile = false;
      }
    });
  }

  getStatusKyc() {
    this.registerService.getStatusKyc().subscribe({
      next: ({ data }) => {
        this.kycStatus =
          data.getStatusKyc.merchantRegistration?.merchantKyc.status;
      }
    });
  }

  goToBenefit() {
    this.gaService.addEvent(
      'Ajakcuan_click_lihat_lainnya',
      'ajakcuan',
      '',
      true
    );
    this.router.navigate(['/ajakcuan/benefit']);
  }

  handleCreateAgent() {
    this.loadingCreateAgent = true;
    const data: CreateAgentInputDto = {
      createAgentInput: {
        referralCode: this.createAgentForm.controls['referralCode'].value
      }
    };

    this.ajakCuanService.createAgent(data).subscribe({
      next: () => {
        this.loadingCreateAgent = false;
        this.gaService.addEvent(
          'Ajakcuan_input_kode_referral_correct',
          'ajakcuan',
          '',
          true
        );
        this.router.navigate(['/ajakcuan/succeed'], {
          replaceUrl: true
        });
      },
      error: (error) => {
        this.loadingCreateAgent = false;
        this.gaService.addEvent(
          'Ajakcuan_input_kode_referral_wrong',
          'ajakcuan',
          '',
          true
        );
        const [graphqlError] = error.graphQLErrors;
        this.errorMessage =
          graphqlError?.message === this.defaultErrorMessage
            ? 'Kode Referral tidak boleh kosong...'
            : graphqlError?.message;
        if (graphqlError?.message) {
          this.invalidReferral = true;
        }
      }
    });
  }

  handleTncApproved() {
    this.gaService.addEvent(
      'Ajakcuan_click_button_saya_setuju',
      'ajakcuan',
      '',
      true
    );
    this.check = true;
  }

  handleChange() {
    this.invalidReferral = false;
  }

  handleShowTncModal() {
    const modalRef = this.modalService.open(TncAjakCuanModalComponent, {
      title: 'Syarat & Ketentuan',
      closeButton: true,
    });
    modalRef.onResult().subscribe((data) => {
      this.check = data;
      this.gaService.addEvent(
        'Ajakcuan_click_syarat_ketentuan',
        'ajakcuan',
        '',
        true
      );
    })
  }
}
