import { Component } from '@angular/core';
import {Modal} from "../../../shared/service/modal/modal.model";
import {InputModalDto} from "../../../shared/service/modal/modal.service";
import {GaService} from "../../../shared/service/ga.service";

@Component({
  selector: 'tnc-ajak-cuan-modal',
  templateUrl: './tnc-ajak-cuan-modal.component.html',
})
export class TncAjakCuanModalComponent extends Modal{
  title!: string;
  showButton = false;

  constructor(
    private gaService: GaService
  ) {
    super();
  }

  onInjectInputs(inputs: InputModalDto) {
    this.title = inputs.title;
    this.showButton = inputs.closeButton;
  }

  handleTncApproved(value: boolean) {
    this.gaService.addEvent(
      'Ajakcuan_click_button_saya_setuju',
      'ajakcuan',
      '',
      true
    );
    this.close(value)
  }

  cancel(): void {
    this.gaService.addEvent(
      'Ajakcuan_click_button_cross_inSK',
      'ajakcuan',
      '',
      true
    );
  }
}
