import { Injectable } from "@angular/core";
import { Resolve } from "@angular/router";
import { Observable } from 'rxjs';
import { ScmAjakCuanService } from "@youtap-web-lib";


@Injectable({
    providedIn: 'root'
})
export class AjakcuanResolver implements Resolve<boolean> {
    constructor(private scmAjakcuanService: ScmAjakCuanService) { }

    resolve(): Observable<boolean> | Promise<boolean> | boolean {
        return this.scmAjakcuanService.detailAgent().toPromise().then(({ data }: any) => {
            if (data.detailAgent.status === 'AGENT_STATUS_ACTIVE') {
                return true;
            } else {
                return false;
            }
        }).catch(() => {
            return false;
        })
    }
}
