import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'success-register',
  templateUrl: './success-register.component.html',
  styleUrl: './success-register.component.scss'
})
export class SuccessRegisterComponent {
  constructor(private router: Router, private gaService: GaService) {}

  goToAjakCuan() {
    this.gaService.addEvent(
      'Ajakcuan_click_yuk_mulai_sekarang',
      'ajakcuan',
      '',
      true
    );
    this.router.navigate(['/ajakcuan/dashboard'], {
      replaceUrl: true
    });
  }
}
