import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { CodeInputComponent } from "angular-code-input";
import {
  AjakCuanService,
  BalanceService,
  CreateWithDrawCommission,
  PinAuthorizationInputDto,
  WithDrawCommissionInputDto
} from "@youtap-web-lib";
import { ActivatedRoute, Router } from "@angular/router";
import { Location } from '@angular/common';
import { AuthData } from "../../../shared/data/auth.data";
import {
  SuccessModalWithdrawCommissionComponent
} from "../withdraw-commission/success-modal-withdraw-commission/success-modal-withdraw-commission.component";
import {
  FailedModalWithdrawCommissionComponent
} from "../withdraw-commission/failed-modal-withdraw-commission/failed-modal-withdraw-commission.component";
import { ModalService } from "../../../shared/service/modal/modal.service";
import { WindowActionService } from "../../../shared/service/window-action.service";
import { GaService } from "../../../shared/service/ga.service";

@Component({
  selector: 'pin-ajak-cuan',
  templateUrl: './pin-ajak-cuan.component.html',
  styleUrl: './pin-ajak-cuan.component.scss'
})
export class PinAjakCuanComponent implements OnInit, AfterViewInit {
  @ViewChild('codeInput') codeInput!: CodeInputComponent;
  error = false;
  errorMessage!: string;
  amountReceived: string = '';
  loadingVerified: boolean = false;
  showBlockedAccountModal: boolean = false;

  constructor(
    private ajakCuanService: AjakCuanService,
    private authData: AuthData,
    private balanceService: BalanceService,
    private modalService: ModalService,
    private route: ActivatedRoute,
    private windowActionService: WindowActionService,
    private gaService: GaService,
    private location: Location,
    private router: Router,
  ) { }

  ngOnInit() {
    this.amountReceived = this.route.snapshot.queryParams['amountReceived'] || '';
  }

  ngAfterViewInit(): void {
    this.codeInput.focusOnField(0);
  }

  onCodeCompleted(code: string) {
    this.loadingVerified = true;
    const data: PinAuthorizationInputDto = {
      pinAuthorizationInput: {
        pin: code,
      }
    };

    this.ajakCuanService.pinAuthorization(data).subscribe({
      next: ({ data }) => {
        this.loadingVerified = false;
        this.gaService.addEvent(
          'tarik_komisi_pin_komisi_success',
          'ajakcuan',
          '',
          true
        );
        if (data) {
          this.authData.withdrawToken = data.pinAuthorization.authToken.accessToken;
          this.windowActionService.dispatchEvent('withdrawToken', {
            value: data.pinAuthorization.authToken.accessToken
          });
          this.handleWithDrawCommission();
          this.error = false;
        }
      }, error: (error) => {
        this.loadingVerified = false;
        this.gaService.addEvent(
          'tarik_komisi_pin_komisi_invalid',
          'ajakcuan',
          '',
          true
        );
        if (error?.message === 'Akun atau PIN yang kamu masukkan salah') {
          this.errorMessage = 'PIN Tidak Sesuai';
          this.error = true;
          this.codeInput.reset();
          this.codeInput.focusOnField(0);
        } else {
          this.showBlockedAccountModal = true;
          this.error = true;
          this.codeInput.reset();
          this.codeInput.focusOnField(0);
        }
      }
    })
  };

  handleWithDrawCommission() {
    const commissionInput: CreateWithDrawCommission = {
      amount: parseFloat(this.amountReceived),
    };
    const data: WithDrawCommissionInputDto = {
      withDrawCommissionInput: commissionInput
    };
    this.balanceService.withDrawCommission(data).subscribe({
      next: () => {
        this.authData.withdrawToken = '';
        this.windowActionService.dispatchEvent('withdrawToken', {
          value: '',
        });
        this.gaService.addEvent(
          'Ajakcuan_click_syarat_ketentuan_intarik_komisi_page',
          'ajakcuan',
          '',
          true
        );
        const modalRef = this.modalService.open(
          SuccessModalWithdrawCommissionComponent,
          {
            title: '',
            closeButton: false,
            data: {
              content: ''
            }
          }
        );
        setTimeout(() => {
          modalRef.close('');
          this.router.navigate(['ajakcuan/detail-withdraw-commission']);
        }, 1500);
      },
      error: () => {
        this.authData.withdrawToken = '';
        this.windowActionService.dispatchEvent('withdrawToken', {
          value: '',
        });
        const modalRef = this.modalService.open(
          FailedModalWithdrawCommissionComponent,
          {
            title: '',
            closeButton: false,
            data: {
              content: ''
            }
          }
        );
        setTimeout(() => {
          modalRef.close('');
          this.router.navigate(['ajakcuan/detail-withdraw-commission']);
        }, 1500);
      }
    });
  }

  handleWhatsApp() {
    this.gaService.addEvent(
      'click_button_hub_costumer_service',
      'ajakcuan',
      '',
      true
    );
    const url: string = 'https://api.whatsapp.com/send?phone=628111500523'
    this.windowActionService.open(url, '_blank');
  }

  handleBack() {
    this.location.historyGo(-1);
  }
}
