import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ModalService } from '../../../shared/service/modal/modal.service';

import { SharePosModalComponent } from '../share-pos-modal/share-pos-modal.component';
import {
  ShareBosModalComponent,
  ShareBosModalDto
} from '../share-bos-modal/share-bos-modal.component';

import { environment } from '../../../../environments/environment';

import {
  AjakCuanService,
  PlanOption,
  ResponseListItemSubDto,
  RequestListItemDto,
  ItemsAjakCuanSCM,
  ScmAjakCuanService,
  ItemSubsAjakCuan
} from '@youtap-web-lib';

@Component({
  selector: 'education-pos',
  templateUrl: './education-pos.component.html',
  styleUrl: './education-pos.component.scss'
})
export class EducationPosComponent implements OnInit {
  loadingPlan = false;

  dataPlan!: ResponseListItemSubDto;
  dataPlanOption: PlanOption[] = [];
  itemsData: ItemsAjakCuanSCM[] = [];

  items = [
    {
      pic: './assets/images/png/ajak-cuan/pos/1.png',
      description: 'PRODUK YOUTAP YANG BISA DIJUAL'
    },
    {
      pic: './assets/images/png/ajak-cuan/pos/2.png',
      description: 'KELEBIHAN PRODUK YOUTAP POS'
    },
    {
      pic: './assets/images/png/ajak-cuan/pos/3.png',
      description: 'TIPE MERCHANT YANG BISA PAKAI YOUTAP POS?'
    },
    {
      pic: './assets/images/png/ajak-cuan/pos/4.png',
      description: 'DOWNLOAD YOUTAP POS'
    },
    {
      pic: './assets/images/png/ajak-cuan/pos/5.png',
      description: 'BAGIKAN LINK PAKET BERLANGGANAN DI BAWAH INI'
    }
  ];

  itemsFaq = [
    {
      title: 'Produk apa yang dapat saya jual?',
      description:
        'Kamu dapat menjual berbagai macam tipe subscrioption kepada calon pembeli. Share service subscription ini tidak hanya dapat dibagikan pada pengguna pos tetapi kepada siapapun yang hendak kamu rekomendasikan',
      isDescriptionVisible: false
    },
    {
      title: 'Kapan saya akan dapat komisi? ',
      description:
        'Komisi akan didapat setelah pembeli melakukan pembelian subscription pada aplikasi Youtap BOS dengan masa tenggang claim komisi selama 7 hari berjalan.',
      isDescriptionVisible: false
    },
    {
      title: 'Bagimana mekanisme komisi terhitung untuk subscription?',
      description:
        'Komisi akan terhitung ketika merchant melakukan pembelian pada susbcription yang agent rekomendasikan dan melakukan pembelian selama dalam masa tenggang claim 7 hari berjalan. Jika merchant tidak membeli subscription yang direkomendasikan dan sudah melebihi dari masa tenggang maka komisi tidak dapat diclaim',
      isDescriptionVisible: false
    },
    {
      title: 'Berapa minimal penarikan komisi yang dapat diajukan oleh agent ? ',
      description:
        'Minimal penarikan agent sebesar Rp. 50.000,- dengan biaya penarikan sebesar Rp.2.500,- dan di kurangin PPH 21.',
      isDescriptionVisible: false
    },
    {
      title: 'Kapan komisi ditransfer ke rekening agent ?',
      description:
        'Komisi ditransfer H+1 setelah agent melakukan pengajuan pada aplikasi BOS. ',
      isDescriptionVisible: false
    }
  ];

  constructor(
    private modalService: ModalService,
    private ajakCuanService: AjakCuanService,
    private router: Router,
    private scmAjakCuanService: ScmAjakCuanService
  ) { }

  ngOnInit(): void {
    this.getListPlan();
  }

  toggleDescription(index: number) {
    this.itemsFaq[index].isDescriptionVisible =
      !this.itemsFaq[index].isDescriptionVisible;
  }

  getListPlan() {
    this.loadingPlan = true;
    const dataPlan: RequestListItemDto = {
      listItemInput: {
        page: 1,
        limit: 25,
        filter: {
          itemType: 'ITEM_TYPE_SUBSCRIPTION'
        }
      }
    };
    this.ajakCuanService.listItem(dataPlan).subscribe({
      next: ({ data }) => {
        this.loadingPlan = false;
        const currentDate = new Date();
        const filteredItems = data.listItem.items.filter(item => {
          if (item.periodEnd !== null) {
            const periodEnd = new Date(item.periodEnd);
            return periodEnd > currentDate;
          }
          return true;
        });

        this.dataPlan = {
          listItem: {
            page: data.listItem.page,
            limit: data.listItem.limit,
            total: data.listItem.total,
            totalPage: data.listItem.totalPage,
            items: filteredItems
          }
        };
      },
      error: () => {
        this.loadingPlan = false;
      }
    });
  }

  goSubs(id: string) {
    this.router.navigate(['/subscription/list-subscription'], {
      queryParams: {
        id: id
      }
    });
  }

  goRegister() {
    this.router.navigate(['/register']);
  }

  generateLink(
    type: string,
    subscription?: ItemSubsAjakCuan
  ) {
    let dataProduct!: ShareBosModalDto;
    if (type === 'subscription' && subscription) {
      dataProduct = {
        name: subscription.planOption.plan.displayName,
        commission: subscription.amount,
        image: subscription.planOption.image,
        link: environment.BOS_URL + '/subscription/list-subscription',
        priceType: subscription.priceType,
        planOptionId: subscription.planOption.id,
      };
    }

    const modalRef = this.modalService.open(ShareBosModalComponent, {
      title: 'Bagikan dan promosikan produk ini',
      closeButton: true,
      data: {
        dataProduct: dataProduct,
        type: type,
        subscriptionId: subscription ? subscription?.id : '',
      }
    });
    modalRef.onResult().subscribe(
      () => {
        // eslint-disable-next-line no-console
        console.log(type);
      },
      (dismissed) => {
        dismissed;
      }
    );
  }

  showSharePOS() {
    const modalRef = this.modalService.open(SharePosModalComponent, {
      title: 'Bagikan dan promosikan produk ini',
      closeButton: true,
      data: {}
    });
    modalRef.onResult().subscribe(
      (data) => {
        // eslint-disable-next-line no-console
        console.log(data);
      },
      (dismissed) => {
        dismissed;
      }
    );
  }
}
