import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import {
  ProductService,
  ProductVariant,
  IsSupplierCoveredDto,
  SupplierService,
  ResponseDetailProductDto,
  DetailProductDto,
  ListProductDto,
  Products,
  ItemType,
  ItemPriceType,
  ItemStatusType,
  ScmAjakCuanService, productVariants,
} from '@youtap-web-lib';
import { AuthData } from '../../../shared/data/auth.data';
import { ReferralService } from '../../../shared/service/referral.service';
import { ModalService } from '../../../shared/service/modal/modal.service';
import {
  ShareBosModalComponent,
  ShareBosModalDto
} from '../share-bos-modal/share-bos-modal.component';
import { GaService } from '../../../shared/service/ga.service';
import {environment} from "../../../../environments/environment.prod";

@Component({
  selector: 'detail-product',
  templateUrl: './detail-product.component.html',
  styleUrls: ['./detail-product.component.scss']
})
export class DetailProductComponent implements OnInit {
  detailProduct!: ResponseDetailProductDto;
  loading = false;
  page = 1;
  shareModal = false;
  link: string = window.location.href;
  message!: string;
  isPOD!: boolean;
  showTnc = false;
  token = '';
  isCovered = true;
  dataListProductMerge: Products[] = [];
  product_slug!: string | null;
  productVariantSelected!: ProductVariant;
  priceBeforeDiscount = 0;
  isFullDescription = true;
  addressMap = '';
  variant_slug!: string | null;
  priceType!: string;
  itemIdProduct!: string;
  commission!: number;
  periodEnd!: string | null;
  items: {
    id: string;
    name: string;
    type: ItemType;
    amount: number;
    image: string;
    periodEnd: string | null;
    periodStart: null;
    status: ItemStatusType;
    maxPrice: number;
    priceType: ItemPriceType;
    productVariant: ProductVariant[];
  }[] = [];

  constructor(
    private productService: ProductService,
    private route: ActivatedRoute,
    private router: Router,
    public authData: AuthData,
    private supplierService: SupplierService,
    private referralService: ReferralService,
    private modalService: ModalService,
    private scmAjakCuanService: ScmAjakCuanService,
    private gaService: GaService
  ) { }

  ngOnInit() {
    this.gaService.addEvent(
      'Ajakcuan_detail_product_indashboard',
      'ajakcuan',
      '',
      true
    );
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.product_slug = this.route.snapshot.paramMap.get('slug') || '';
    this.variant_slug = this.route.snapshot.paramMap.get('variantSlug') || '';
    this.getDetailProduct(this.product_slug);
    this.token = this.authData.token;

    const params = new URLSearchParams(window.location.search);
    this.itemIdProduct = params.get('id') || '';
    this.priceType = params.get('type') || '';
    this.commission = parseFloat(params.get('price') || '');
    this.periodEnd = params.get('periodEnd') || null;
  }

  getDetailProduct(slug: string) {
    this.loading = true;
    const data: DetailProductDto = {
      detailProductInput: {
        ...(slug && { slug: `${slug}` }),
        ...(this.variant_slug && { productVariantSlug: `${this.variant_slug}` })
      },
      warehouseId: ''
    };

    this.productService.detailProduct(data).subscribe({
      next: ({ data }) => {
        this.loading = false;
        const productVariant = data.detailProduct.productVariants
          .filter(
            (variant) =>
              variant.productVariantWarehouses &&
              variant.productVariantWarehouses[0].status === 'ACTIVE'
          )
          .map((val) => {
            val.isReferral = this.referralService.findReferral(val.slug);
            return val;
          });
        const prod: ResponseDetailProductDto = {
          detailProduct: {
            actualPriceMax: data.detailProduct.actualPriceMax,
            actualPriceMin: data.detailProduct.actualPriceMin,
            description: data.detailProduct.description,
            id: data.detailProduct.id,
            image: data.detailProduct.image,
            isActive: data.detailProduct.isActive,
            isPODAvailable: data.detailProduct.isPODAvailable,
            name: data.detailProduct.name,
            activeDiscount: data.detailProduct.activeDiscount,
            productVariants: productVariant.length
              ? productVariant
              : data.detailProduct.productVariants,
            slug: data.detailProduct.slug,
            supplier: data.detailProduct.supplier,
            warehouse: data.detailProduct.warehouse,
            categoryProducts: data.detailProduct.categoryProducts,
            isAvailable: productVariant.filter((stock) => stock.stock > 0)
              .length
              ? data.detailProduct.isAvailable
              : false,
          }
        };
        this.detailProduct = prod;
        if (data.detailProduct.supplier) {
          this.getListProduct(
            data.detailProduct.warehouse ? data.detailProduct.warehouse.id : ''
          );
          this.isSupplierCovered(data?.detailProduct?.supplier.id);
        }
        this.loading = false;
        this.message = data.detailProduct.name;
        const variantHaveStock =
          this.detailProduct.detailProduct.productVariants.filter(
            (variant) => variant.stock > 0
          );
        const productVariantActive =
          this.detailProduct.detailProduct.productVariants.findIndex(
            (productVariant) =>
              variantHaveStock.length
                ? productVariant.productVariantWarehouses &&
                productVariant.stock > 0 &&
                productVariant.productVariantWarehouses[0].status === 'ACTIVE'
                : productVariant.productVariantWarehouses &&
                productVariant.productVariantWarehouses[0].status === 'ACTIVE'
          );
        this.productVariantSelected =
          this.detailProduct.detailProduct.productVariants[
          productVariantActive === -1 ? 0 : productVariantActive
          ];

        this.items = [
          {
            id: this.itemIdProduct,
            name: data.detailProduct.name,
            type: ItemType.ITEM_TYPE_PRODUCT_VARIANT,
            amount: this.commission,
            image: data.detailProduct.image,
            periodEnd: this.periodEnd,
            periodStart: null,
            status: ItemStatusType.ITEM_STATUS_ACTIVE,
            maxPrice: data.detailProduct.actualPriceMax,
            priceType:
              this.priceType === 'ITEM_PRICE_TYPE_PERCENTAGE'
                ? ItemPriceType.ITEM_PRICE_TYPE_PERCENTAGE
                : ItemPriceType.ITEM_PRICE_TYPE_FIXED,
            productVariant: data.detailProduct.productVariants
          }
        ];
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  generateLink(
    type: string,
    product?: {
      id: string;
      name: string;
      type: ItemType;
      amount: number;
      image: string;
      periodEnd: string | null;
      periodStart: null;
      status: ItemStatusType;
      maxPrice: number;
      priceType: ItemPriceType;
      productVariant: productVariants[];
    }
  ) {
    this.gaService.addEvent(
      'Ajakcuan_click_bagikan_indetail_product_dashboard',
      'ajakcuan',
      '',
      true
    );
    let dataProduct!: ShareBosModalDto;
    if (type === 'product' && product) {
      dataProduct = {
        name: product.name,
        commission: product.amount,
        image: product.image,
        link: environment.BOS_URL + '/product/variant-detail/' + this.productVariantSelected.slug,
        priceType: product.priceType
      };
    }
    const modalRef = this.modalService.open(ShareBosModalComponent, {
      title: 'Bagikan dan promosikan produk ini',
      closeButton: true,
      data: {
        dataProduct: dataProduct,
        type: type,
        isForm: 'detail',
        productId: product ? product.id : '',
      }
    });
    modalRef.onResult().subscribe(
      () => {
        // eslint-disable-next-line no-console
        console.log(type);
      },
      (dismissed) => {
        this.gaService.addEvent(
          'Ajakcuan_click_button_cross_indetail_product_dashboard',
          'ajakcuan',
          '',
          true
        );
        dismissed;
      }
    );
  }

  isSupplierCovered(id: string) {
    if (this.token) {
      this.loading = true;
      const data: IsSupplierCoveredDto = {
        supplierId: id
      };
      this.supplierService.isSupplierCovered(data).subscribe({
        next: ({ data }) => {
          this.loading = false;
          this.isCovered = data?.isSupplierCovered;
        },
        error: (error) => {
          this.loading = false;
          return error;
        }
      });
    }
  }

  getListProduct(id: string) {
    this.loading = true;
    const data: ListProductDto = {
      listProductInput: {
        page: this.page,
        limit: 6,
        filter: {
          warehouseId: id,
          isAvailable: true
        },
        order: {
          orderBy: 'UPDATED_AT',
          sortBy: 'ASC'
        }
      },
      warehouseId: ''
    };
    this.productService.listProduct(data).subscribe({
      next: ({ data }) => {
        this.loading = false;
        this.dataListProductMerge = data.listProduct.products.filter(
          (val) => val.slug !== this.product_slug
        );
        if (this.dataListProductMerge.length > 5) {
          this.dataListProductMerge.splice(5, 1);
        }
        this.page += 1;
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  goToDetailSupplier(slug: string) {
    this.router.navigate(['supplier/detail/'], {
      queryParams: {
        ss: slug
      }
    });
  }

  goToAddressList() {
    this.router.navigate(['/address/list']);
  }

  handleShowTnc() {
    this.showTnc = true;
  }
}
