import { CurrencyPipe } from '@angular/common';
import { Component, OnInit} from '@angular/core';
import {NavigationEnd, Router} from '@angular/router';
import {
  BalanceService,
} from '@youtap-web-lib';
import {filter, Subject} from "rxjs";
import { GaService } from '../../../../shared/service/ga.service';

@Component({
  selector: 'saldo-withdraw-commission',
  templateUrl: './saldo-withdraw-commission.component.html',
  styleUrl: './saldo-withdraw-commission.component.scss'
})
export class SaldoWithdrawCommissionComponent implements OnInit {
  formattedAmount: string = '0';
  minMaxMessage: string = 'Minimum penarikan Rp50.000';
  messageError!: string;

  totalCommission!: number;
  costDisburment!: number;
  amountReceived!: number;
  valueWithDraw!: number;

  error: boolean = false;
  checkedTnc: boolean = false;
  showTnCModal: boolean = false;
  showTnTwoCModal: boolean = false;
  loadingBalance: boolean = false;
  loadingWithDraw: boolean = false;

  constructor(
    private currencyPipe: CurrencyPipe,
    private balanceService: BalanceService,
    private router: Router,
    private gaService: GaService,
  ) {}

  ngOnInit(): void {
    this.getAffiliateBalance();
    this.costDisburment = 2500;
    this.handleGaInfoGetCommission();

    this.router.events.pipe(
      filter(event => event instanceof NavigationEnd)
    ).subscribe(() => {
      this.error = false;
      this.formattedAmount = '0';
      this.messageError = '';
      this.getAffiliateBalance();
    });
  }

  handleGaInfoGetCommission() {
    if (this.showTnCModal === true) {
      this.gaService.addEvent(
        'Ajakcuan_click_button_i_intarik_komisi_page',
        'ajakcuan',
        '',
        true
      );
    } else if (this.checkedTnc === true) {
      this.gaService.addEvent(
        'Ajakcuan_click_syarat_ketentuan_intarik_komisi_page',
        'ajakcuan',
        '',
        true
      );
    }
  }

  getAffiliateBalance() {
    this.loadingBalance = true;
    this.balanceService.getAffiliateBalance().subscribe({
      next: ({ data }) => {
        this.loadingBalance = false;
        this.totalCommission = data.getAffiliateBalance.balance;
      },
      error: () => {
        this.loadingBalance = false;
      }
    });
  }

  onInput(event: Event): void {
    const inputValue = (event.target as HTMLInputElement).value;
    const numericValue = parseFloat(inputValue.replace(/[^\d]/g, ''));

    this.valueWithDraw = numericValue;

    // validation check numericValue and convert to currency rupiah
    if (!isNaN(numericValue)) {
      const valueRupiah =
        this.currencyPipe.transform(numericValue, 'IDR', '', '1.0-0') || '';
      this.formattedAmount = this.formattedRupiah(valueRupiah);
    } else {
      this.formattedAmount = '0';
    }

    // check input > total commission && input < minimum withdraw
    this.checkValidationInput(numericValue);

    // calculate withdraw commission
    this.calculateWithDrawCommission();
  }

  numberOnly(event: KeyboardEvent) {
    const charCode = event.which ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  formattedRupiah(value: string) {
    return value.replace(/,/g, '.');
  }

  formatCommissionNumber(value: number, currency: string, symbol: string) {
    const valueRupiah =
      this.currencyPipe.transform(value, currency, symbol, '1.0-0') || '';
    return this.formattedRupiah(valueRupiah);
  }

  formatCommissionString(value: string, currency: string, symbol: string) {
    const toString = value.toString();
    const numericValue = toString.replace(/[^\d]/g, '');
    const valueRupiah =
      this.currencyPipe.transform(
        parseFloat(numericValue),
        currency,
        symbol,
        '1.0-0'
      ) || '';
    return this.formattedRupiah(valueRupiah);
  }

  checkValidationInput(value: number) {
    const minimumWithDraw = 50000;
    if (value > this.totalCommission) {
      this.gaService.addEvent(
        'Ajakcuan_input_nominal_lebih_besar_dari_total_komisi',
        'ajakcuan',
        '',
        true
      );
      this.messageError = 'Penarikan tidak boleh lebih besar dari Total Komisi';
      this.error = false;
      this.checkedTnc = false;
    } else if (value < minimumWithDraw) {
      this.error = true;
      this.checkedTnc = false;
    } else {
      this.error = false;
      this.messageError = '';
    }
  }

  calculateWithDrawCommission() {
    if (this.formattedAmount !== '0') {
      this.amountReceived = this.valueWithDraw;
    } else {
      this.amountReceived = 0;
    }
  }

  handleWithDrawCommission() {
    this.router.navigate(['/ajakcuan/input-pin'], {
      queryParams: {
        amountReceived: this.amountReceived,
      }
    })
  }

  withdrawAll() {
    this.formattedAmount = this.totalCommission.toString();
    this.valueWithDraw = this.totalCommission;
    this.checkValidationInput(this.totalCommission);
    this.calculateWithDrawCommission();
  }

  handleTncApproved() {
    this.gaService.addEvent(
      'Ajakcuan_click_button_saya_setuju_intarik_komisi',
      'ajakcuan',
      '',
      true
    );
    this.checkedTnc = true;
    this.showTnTwoCModal = false;
  }

  myRefreshEvent(event: Subject<void>): void {
    this.error = false;
    this.formattedAmount = '0';
    this.messageError = '';
    this.getAffiliateBalance();
    setTimeout(() => {
      event.next();
    }, 2000);
  }
}
