import { AfterViewInit, Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from "@angular/router";
import { WindowActionService } from "../../../shared/service/window-action.service";
import { AuthData } from "../../../shared/data/auth.data";
import { NavController } from '@ionic/angular';

@Component({
  selector: 'footer-ajak-cuan',
  templateUrl: './footer-ajak-cuan.component.html',
})
export class FooterAjakCuanComponent implements OnInit, AfterViewInit {
  token = "";
  currentUrl = "/";

  constructor(
    private router: Router,
    private windowActionService: WindowActionService,
    private authData: AuthData,
    private route: ActivatedRoute,
    private navCtrl: NavController,
  ) {
  }

  ngOnInit(): void {
    this.currentUrl = this.windowActionService.getPathName();
    this.router.events.subscribe((val) => {
      if (val instanceof NavigationEnd) {
        this.currentUrl = this.windowActionService.getPathName();
      }
    });

    this.route.data.subscribe(data => {
      if (data['data']) {
        this.navCtrl.navigateRoot("/ajakcuan/dashboard", { replaceUrl: true });
      } else {
        this.navCtrl.navigateRoot("/ajakcuan/onboard", { replaceUrl: true });
      }
    });
  }

  ngAfterViewInit() {
    this.token = this.authData.token;
  }

  toHome() {
    this.router.navigate(["/ajakcuan/dashboard"]);
  }

  toPerformance() {
    this.router.navigate(["/ajakcuan/performance"]);
  }

  toEducation() {
    this.router.navigate(["/ajakcuan/education"]);
  }
}
