import { Component, OnInit } from '@angular/core';
import { BalanceService } from '@youtap-web-lib';
import { CurrencyPipe } from '@angular/common';
import * as dayjs from 'dayjs';
import idLocale from 'dayjs/locale/id';

dayjs.locale(idLocale);

interface FilterDateArray {
  key: string;
  label: string;
  startDate: string;
  endDate: string;
  startDateLabel?: Date;
  endDateLabel?: Date;
}

@Component({
  selector: 'performance-ajak-cuan',
  templateUrl: './performance-ajak-cuan.component.html',
  styleUrl: './performance-ajak-cuan.component.scss'
})
export class PerformanceAjakCuanComponent implements OnInit {
  totalCommission: number = 0;
  loadingBalance = false;
  dateLabel = '';
  sectionSelected = 'product';
  isShowDateModal = false;
  isShowCalendarModal = false;
  dateSelected!: FilterDateArray | undefined;
  startDate = new Date();
  endDate = new Date();
  date: Date[] | undefined;

  filterDateArray: FilterDateArray[] = [
    {
      key: 'month',
      label: '30 hari terakhir',
      startDate: dayjs(this.formatDateFunction(31)).format('YYYY-MM-DD'),
      endDate: dayjs(new Date()).format('YYYY-MM-DD'),
      startDateLabel: new Date(this.formatDateFunction(31)),
      endDateLabel: new Date()
    },
    {
      key: 'week',
      label: '14 hari terakhir',
      startDate: dayjs(this.formatDateFunction(14)).format('YYYY-MM-DD'),
      endDate: dayjs(new Date()).format('YYYY-MM-DD'),
      startDateLabel: new Date(this.formatDateFunction(14)),
      endDateLabel: new Date()
    },
    {
      key: 'yesterday',
      label: 'Kemarin',
      startDate: dayjs(this.formatDateFunction(1)).format('YYYY-MM-DD'),
      endDate: dayjs(new Date()).format('YYYY-MM-DD'),
      startDateLabel: new Date(this.formatDateFunction(1)),
      endDateLabel: new Date()
    },
    {
      key: 'today',
      label: 'Hari ini',
      startDate: dayjs(new Date()).format('YYYY-MM-DD'),
      endDate: dayjs(new Date()).format('YYYY-MM-DD'),
      startDateLabel: new Date(),
      endDateLabel: new Date()
    },
    {
      key: 'custom',
      label: 'Atur tanggal',
      startDate: dayjs(this.startDate).format('YYYY-MM-DD'),
      endDate: dayjs(this.endDate).format('YYYY-MM-DD'),
      startDateLabel: this.startDate,
      endDateLabel: this.endDate
    }
  ];

  constructor(
    private balanceService: BalanceService,
    private currencyPipe: CurrencyPipe
  ) {}

  ngOnInit() {
    this.dateSelected = {
      key: 'month',
      label: '30 hari terakhir',
      startDate: dayjs(this.formatDateFunction(31)).format('YYYY-MM-DD'),
      endDate: dayjs(new Date()).format('YYYY-MM-DD'),
      startDateLabel: this.formatDateFunction(31),
      endDateLabel: new Date()
    };
    this.dateLabel = `${this.dateSelected?.label} (${this.dateSelected?.startDate} - ${this.dateSelected?.endDate})`;
    this.getAffiliateBalance();
  }

  formatDateFunction(day: number) {
    const millisecondsInADay = 24 * 60 * 60 * 1000;
    return new Date(new Date().getTime() - day * millisecondsInADay);
  }

  getAffiliateBalance() {
    this.loadingBalance = true;
    this.balanceService.getAffiliateBalance().subscribe({
      next: ({ data }) => {
        this.loadingBalance = false;
        this.totalCommission = data.getAffiliateBalance.balance;
      },
      error: () => {
        this.loadingBalance = false;
      }
    });
  }

  formattedRupiah(value: string) {
    return value.replace(/,/g, '.');
  }

  formatCommissionNumber(value: number, currency: string, symbol: string) {
    const valueRupiah =
      this.currencyPipe.transform(value, currency, symbol, '1.0-0') || '';
    return this.formattedRupiah(valueRupiah);
  }

  changeSection(section: string) {
    this.sectionSelected = section;
  }

  selectFilterDate() {
    const customIndex = this.filterDateArray.findIndex(
      (item) => item.key === 'custom'
    );
    if (this.dateSelected?.key === 'custom') {
      if (customIndex !== -1) {
        this.filterDateArray[customIndex] = {
          key: 'custom',
          label: 'Atur tanggal',
          startDate: dayjs(this.startDate).format('YYYY-MM-DD'),
          endDate: dayjs(this.endDate).format('YYYY-MM-DD'),
          startDateLabel: this.startDate,
          endDateLabel: this.endDate
        };
      }
      this.dateSelected = this.filterDateArray[customIndex];
    }
    this.dateLabel = `${this.dateSelected?.label} (${this.dateSelected?.startDate} - ${this.dateSelected?.endDate})`;
    this.isShowDateModal = !this.isShowDateModal;
  }

  handleShowDate() {
    this.isShowDateModal = true;
  }

  handleShowCalendar() {
    this.isShowDateModal = false;
    this.isShowCalendarModal = true;
  }

  handleBackToDateModal() {
    this.isShowCalendarModal = false;
    this.isShowDateModal = true;
  }

  onDateChange(newDateValue: Date[]): void {
    this.startDate = newDateValue[0];
    this.endDate = newDateValue[1];
    if (newDateValue[1]) {
      this.handleBackToDateModal();
    }
  }
}
