import { Component } from '@angular/core';
import { Clipboard } from '@angular/cdk/clipboard';

import { Modal } from '../../../shared/service/modal/modal.model';
import { InputModalDto } from '../../../shared/service/modal/modal.service';
import { GaService } from '../../../shared/service/ga.service';
import {RequestGenerateLinkAgentItemDto, ScmAjakCuanService, Tier} from '@youtap-web-lib';
import {environment} from "../../../../environments/environment.prod";

export interface ShareBosModalDto {
  image: string;
  name: string;
  commission: number;
  priceType: string;
  periodEnd?: Date | string | null;
  imageTier?: Tier;
  link: string;
  planOptionId?: string;
  supplierSlug?: string;
}

@Component({
  selector: 'share-bos-modal',
  templateUrl: './share-bos-modal.component.html',
  styleUrl: './share-bos-modal.component.scss'
})
export class ShareBosModalComponent extends Modal {
  title!: string;
  showButton = false;
  isLoading = false;
  showToast: boolean = false;
  dataProduct!: ShareBosModalDto;
  type!: string;
  message = '';
  from = '';
  isFrom = '';
  productId = '';
  subscriptionId = '';
  supplierId = '';
  linkToShare = '';

  constructor(
    private clipboard: Clipboard,
    private gaService: GaService,
    private scmAjakCuanService: ScmAjakCuanService,
  ) {
    super();
  }

  onInjectInputs(inputs: InputModalDto): void {
    this.title = inputs.title;
    this.showButton = inputs.closeButton;
    this.dataProduct = inputs.data.dataProduct;
    this.type = inputs.data.type;
    this.from = inputs.data.trackFrom;
    this.isFrom = inputs.data.isForm;
    this.productId = inputs.data.productId;
    this.subscriptionId = inputs.data.subscriptionId;
    this.supplierId = inputs.data.supplierId;
  }

  generateLink(typeLink: string) {
    const dataInput: RequestGenerateLinkAgentItemDto = {
      generateLinkAgentItemInput: {
        itemId: (
          this.productId ? this.productId :
            this.subscriptionId ? this.subscriptionId :
              this.supplierId
        ),
        link: this.dataProduct.link
      }
    }
    this.scmAjakCuanService.generateLinkAgentItem(dataInput).subscribe({
      next: ({data}) => {
        if (data) {
          if (this.productId) {
            this.linkToShare = data.generateLinkAgentItem.path;
          } else if (this.subscriptionId) {
            this.linkToShare = environment.BOS_URL +
              '/subscription/list-subscription' +
              '?id=' +
              this.dataProduct.planOptionId +
              '&refcode=' +
              data.generateLinkAgentItem.code;
          } else if (this.supplierId) {
            this.linkToShare = environment.BOS_URL +
              '/subscription/list-subscription' +
              '?id=' +
              this.dataProduct.supplierSlug +
              '&refcode=' +
              data.generateLinkAgentItem.code;
          }
          if (typeLink === 'wa') {
            this.shareWhatsapp();
          } else if (typeLink === 'link') {
            this.copyLink();
          }
        }
      }
    })
  }

  cancel(): void {
    this.gaService.addEvent(
      'Ajakcuan_click_button_cross_insupplier_modal',
      'ajakcuan',
      '',
      true
    );
    this.dismiss();
  }

  onShare(data: string) {
    this.close(data);
  }

  shareWhatsapp() {
    const url = this.linkToShare;
    if (this.type === 'product') {
      this.gaService.addEvent(
        'Ajakcuan_click_button_whatsapp' + this.from,
        'ajakcuan',
        '',
        true
      );
      if (this.isFrom === 'detail') {
        this.gaService.addEvent(
          'Ajakcuan_click_button_whatsapp_indetail_product_dashboard',
          'ajakcuan',
          '',
          true
        );
      } else {
        this.gaService.addEvent(
          'Ajakcuan_click_button_whatsapp_indashboard',
          'ajakcuan',
          '',
          true
        );
      }
      this.message = `whatsapp://send?text=Beli ${this.dataProduct.name}  ${url}`;
    } else {
      this.gaService.addEvent(
        'Ajakcuan_click_button_salin_link' + this.from,
        'ajakcuan',
        '',
        true
      );
      this.gaService.addEvent(
        'Ajakcuan_click_button_whatsapp_inpaket_extra_mantap',
        'ajakcuan',
        '',
        true
      );
      this.message = `whatsapp://send?text=Berlangganan Paket ${this.dataProduct.name} Sekarang! Nikmati banyak benefit dan rewardnya. ${url}`;
    }
    if(this.type === 'supplier') {
      this.gaService.addEvent(
        'Ajakcuan_click_button_whatsapp_insupplier_modal',
        'ajakcuan',
        '',
        true
      );
    }
    const shareLink = this.message;
    window.open(shareLink, '_blank');
  }

  copyLink() {
    if (this.type === 'product') {
      if (this.isFrom === 'detail') {
        this.gaService.addEvent(
          'Ajakcuan_click_button_salin_link_indetail_product_dashboard',
          'ajakcuan',
          '',
          true
        );
      } else {
        this.gaService.addEvent(
          'Ajakcuan_click_button_salin_link_indashboard',
          'ajakcuan',
          '',
          true
        );
      }
    } else if(this.type === 'supplier') {
      this.gaService.addEvent(
        'Ajakcuan_click_button_salin_link_insupplier_modal',
        'ajakcuan',
        '',
        true
      );
    } else {
      this.gaService.addEvent(
        'Ajakcuan_click_button_salin_link_inpaket_extra_mantap',
        'ajakcuan',
        '',
        true
      );
    }
    this.clipboard.copy(this.linkToShare);
    this.showToast = true;
    setTimeout(() => {
      this.showToast = false;
    }, 2000);
  }
}
