import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ModalService } from '../../../shared/service/modal/modal.service';
import {
  ShareBosModalComponent,
  ShareBosModalDto
} from '../share-bos-modal/share-bos-modal.component';

import { environment } from '../../../../environments/environment.prod';

import {
  ItemType,
  ListItemDto,
  ScmAjakCuanService,
  ItemsAjakCuanSCM,
  ItemSubsAjakCuan,
} from '@youtap-web-lib';

@Component({
  selector: 'education-bos',
  templateUrl: './education-bos.component.html',
  styleUrl: './education-bos.component.scss'
})
export class EducationBosComponent implements OnInit {
  loadingListItem = false;
  itemsData: ItemsAjakCuanSCM[] = [];

  items = [
    {
      pic: './assets/images/png/ajak-cuan/pos/1.png',
      description: 'PRODUK YOUTAP YANG BISA DIJUAL'
    },
    {
      pic: './assets/images/png/ajak-cuan/pos/2.png',
      description: 'KELEBIHAN PRODUK YOUTAP BOS'
    },
    {
      pic: './assets/images/png/ajak-cuan/pos/3.png',
      description: 'SEMUA BISA BELANJA DI BELANJA STOK YOUTAP'
    },
    {
      pic: './assets/images/png/ajak-cuan/pos/4.png',
      description: 'DOWNLOAD YOUTAP BOS'
    },
    {
      pic: './assets/images/png/ajak-cuan/pos/5.png',
      description: 'JUAL DENGAN BAGIKAN LINK PRODUK DI BAWAH INI:'
    }
  ];

  itemsFaq = [
    {
      title: 'Produk apa yang dapat saya jual ?',
      description:
        'Pada katalog produk terdapat beberapa barang rekomendasi seperti paket usaha dan beberapa barang pilihan yang bisa kamu rekomendasikan kepada calon pembeli.',
      isDescriptionVisible: false
    },
    {
      title: 'Kapan saya akan dapat komisi?',
      description:
        'Kamu akan mendapatkan komisi setelah produk yang kamu bagikan terjual dan status transaksinya telah dinyatakan selesai oleh system Youtap BOS.',
      isDescriptionVisible: false
    },
    {
      title: 'Bagimana mekanisme komisi terhitung untuk Paket usaha dan Produk lainnya ?',
      description:
        'Komisi akan terhitung ketika merchant melakukan pembelian pada produk yang direkomendasikan dan melakukan pembelian selama dalam masa tenggang claim 7 hari berjalan. Jika merchant tidak membeli produk yang direkomendasikan dan sudah melebihi dari masa tenggang maka komisi tidak dapat diclaim ',
      isDescriptionVisible: false
    },
    {
      title: 'Berapa minimal penarikan komisi yang dapat diajukan oleh agent ?',
      description:
        'Minimal penarikan agent sebesar Rp. 50.000,- dengan biaya penarikan sebesar Rp.2.500,- dan di kurangin PPH 21. ',
      isDescriptionVisible: false
    },
    {
      title: 'Kapan komisi ditransfer ke rekening agent ?',
      description:
        'Komisi ditransfer H+1 setelah agent melakukan pengajuan pada aplikasi BOS.',
      isDescriptionVisible: false
    }
  ];

  constructor(
    private modalService: ModalService,
    private scmAjakCuanService: ScmAjakCuanService,
    private router: Router,
  ) { }

  ngOnInit(): void {
    this.getListItem();
  }

  toggleDescription(index: number) {
    this.itemsFaq[index].isDescriptionVisible =
      !this.itemsFaq[index].isDescriptionVisible;
  }

  getListItem() {
    this.loadingListItem = true;
    const data: ListItemDto = {
      listItemInput: {
        page: 1,
        limit: 25,
        filter: {
          itemType: ItemType.ITEM_TYPE_PRODUCT_VARIANT
        },
        search: '',
      }
    };

    this.scmAjakCuanService.listItem(data).subscribe({
      next: (data) => {
        this.loadingListItem = false;
        this.itemsData = data.data.listItem.items.filter(
          (item) => !!item.productVariant
        ).filter((product) => product.productVariant.product.supplier.status === 'ACTIVE')
          .filter((variants) =>
            variants.productVariant.productVariantWarehouses.find(
              (status) => status.status === 'ACTIVE'
            )
          );
      },
      error: () => {
        this.loadingListItem = false;
      }
    });
  }

  generateLink(
    type: string,
    product?: ItemsAjakCuanSCM,
    subscription?: ItemSubsAjakCuan
  ) {
    let dataProduct!: ShareBosModalDto;
    if (type === 'product' && product) {
      dataProduct = {
        name: product.productVariant.name,
        commission: product.amount,
        image: product.productVariant.product.image,
        link: environment.BOS_URL + '/product/variant-detail/' + product?.productVariant.slug,
        priceType: product.priceType,
      };
    } else if (type === 'subscription' && subscription) {
      dataProduct = {
        name: subscription.planOption.plan.displayName,
        commission: subscription.amount,
        image: subscription.planOption.image,
        link: environment.BOS_URL + '/subscription/list-subscription',
        priceType: subscription.priceType,
        planOptionId: subscription.planOption.id,
      };
    }

    const modalRef = this.modalService.open(ShareBosModalComponent, {
      title: 'Bagikan dan promosikan produk ini',
      closeButton: true,
      data: {
        dataProduct: dataProduct,
        type: type,
        productId: product ? product.id : '',
        subscriptionId: subscription ? subscription.id : '',
      }
    });
    modalRef.onResult().subscribe(
      () => {
        // eslint-disable-next-line no-console
        console.log(type);
      },
      (dismissed) => {
        dismissed;
      }
    );
  }

  goDetailProduct(slug: string, itemId: string, priceType: string, price: number) {
    this.router.navigate([`ajakcuan/detail-product/${slug}`], {
      queryParams: {
        id: itemId,
        type: priceType,
        price: price,
      }
    }).then(() => {
    });
  }
}
