import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'education',
  templateUrl: './education.component.html',
  styleUrl: './education.component.scss'
})
export class EducationComponent {
  items = [
    {
      title: 'Apa itu Agen Ajak Cuan?',
      description:
        'Agen Ajak Cuan adalah program yang memungkinkan kamu mendapatkan komisi dari setiap produk yang terjual melalui link produk yang kamu bagikan di platform Agen Ajak Cuan pada Youtap BOS ke whatsapp atau salin link. ',
      isDescriptionVisible: false
    },
    {
      title: ' Apa keuntungan mengikuti Agen Ajak Cuan?',
      description:
        'Kamu tidak perlu mengeluarkan modal untuk mengikuti program ini dan kamu bisa mendapatkan penghasilan tambahan hanya dengan merekomendasikan produk ke orang orang disekitarmu. ',
      isDescriptionVisible: false
    },
    {
      title: 'Bagaimana cara mendaftar menjadi agen?',
      description:
        'Untuk menjadi Agen, kamu dapat langsung daftar melalui menu Agen Ajak Cuan di aplikasi Youtap BOS atau kamu bisa mencari informasi lebih pada website youtap.id',
      isDescriptionVisible: false
    },
    {
      title: 'Apa syarat untuk mengikuti Agen Ajak Cuan?',
      description:
        'Kamu hanya perlu memiliki akun Youtap BOS dan akun kamu telah terverifikasi',
      isDescriptionVisible: false
    },
    {
      title: 'Apakah rekening penarikan komisi dapat diganti?',
      description:
        'Untuk mengganti rekening penerima pencairan komisi dapat dilakukan dengan menghubungi Customer Service Youtap.',
      isDescriptionVisible: false
    },
    {
      title: 'Dimana saya dapat menemukan informasi lebih mengenai produk?',
      description:
        'Segala informasi tentang produk dapat agent dapatkan pada halaman edukasi pada dashboard agent.',
      isDescriptionVisible: false
    }
  ];

  isDescriptionVisible = false;
  from = '';

  constructor(private router: Router, private gaService: GaService) {}

  goToEducationPOS() {
    this.gaService.addEvent(
      'Ajakcuan_click_youtap_pos' + this.from,
      'ajakcuan',
      '',
      true
    );
    this.router.navigate(['/ajakcuan/education/pos']);
  }

  goToEducationBOS() {
    this.gaService.addEvent(
      'Ajakcuan_click_youtap_bos' + this.from,
      'ajakcuan',
      '',
      true
    );
    this.router.navigate(['/ajakcuan/education/bos']);
  }

  toggleDescription(index: number) {
    this.gaService.addEvent(
      'Ajakcuan_click_arrow_faq' + this.from,
      'ajakcuan',
      '',
      true
    );
    this.items[index].isDescriptionVisible =
      !this.items[index].isDescriptionVisible;
  }
}
