import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';


import {
  AjakCuanService,
  PlanOption,
  ResponseListItemSubDto,
  RequestListItemDto
} from '@youtap-web-lib';

@Component({
  selector: 'education-pos-landingpage-component',
  templateUrl: './education-pos-landingpage-component.component.html',
  styleUrl: './education-pos-landingpage-component.component.scss'
})
export class EducationPosLandingpageComponentComponent implements OnInit {

  loading = false;
  loadingPlan = false;

  dataPlan!: ResponseListItemSubDto;
  dataPlanOption: PlanOption[] = [];

  dataFeatureCard = [
    {
      image: './assets/images/svg/ajak-cuan/kasir-digital.svg',
      title: 'Kasir Digital',
      description: 'Mudahkan proses pemesanan hingga pembayaran dalam satu aplikasi'
    },
    {
      image: './assets/images/svg/ajak-cuan/ic-cc.svg',
      title: 'Cash & Cashless',
      description: 'Terima pembayaran tunai dan non-tunai dengan QRIS dari M-Bankig atau e-wallet manapun'
    },
    {
      image: './assets/images/svg/ajak-cuan/ic-kp.svg',
      title: 'Katalog Produk',
      description: 'Bisa masukkan produk sesuai keinginan'
    },
    {
      image: './assets/images/svg/ajak-cuan/ic-ap.svg',
      title: 'Analisa Pintar',
      description: 'Mudahkan proses pemesanan hingga pembayaran dalam satu aplikasi'
    },
    {
      image: './assets/images/svg/ajak-cuan/ic-is.svg',
      title: 'Stok Toko',
      description: 'Bisa tahu sisa produk, minimum stok, dan update beberapa produk sekaligus.'
    },
    {
      image: './assets/images/svg/ajak-cuan/ic-wsl.svg',
      title: 'Whatsapp Laporan Harian',
      description: 'Bisa cek laporan penjualan harian dari HP'
    }
  ]

  constructor(
    private router: Router,
    private location: Location,
    private ajakCuanService: AjakCuanService
  ) { }

  ngOnInit(): void {
    this.getListPlan();
  }

  goRegister() {
    this.router.navigate(['/register']);
  }

  goSubs(id: string) {
    this.router.navigate(['/subscription/list-subscription'], {
      queryParams: {
        id: id
      }
    });
  }

  goBack() {
    this.location.back();
  }

  getListPlan() {
    this.loadingPlan = true;
    const dataPlan: RequestListItemDto = {
      listItemInput: {
        page: 1,
        limit: 25,
        filter: {
          itemType: 'ITEM_TYPE_SUBSCRIPTION'
        }
      }
    };
    this.ajakCuanService.listItem(dataPlan).subscribe({
      next: ({ data }) => {
        this.loadingPlan = false;
        this.dataPlan = data
      },
      error: () => {
        this.loadingPlan = false;
      }
    })
  }

}
