import { Component } from '@angular/core';
import {Modal} from "../../../shared/service/modal/modal.model";
import {InputModalDto} from "../../../shared/service/modal/modal.service";
import {Router} from "@angular/router";
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'kyc-verif-modal',
  templateUrl: './kyc-verif-modal.component.html',
})
export class KycVerifModalComponent extends Modal {
  title!: string;
  showButton = false;

  constructor(private router: Router, private gaService: GaService) {
    super();
  }

  onInjectInputs(inputs: InputModalDto) {
    this.title = inputs.title;
    this.showButton = inputs.closeButton;
  }

  handleShowTncModal(value: boolean) {
    this.close(value);
  }

  goToLoginKyc() {
    this.gaService.addEvent(
      'click_ok_kyc',
      'ajakcuan',
      '',
      true
    );
    this.router.navigate(['/register/login-kyc'])
    this.close('');
  }
}
